@font-face {
  font-family: "Arcadia";
  /* src: local("Arcadia"), url("/src/fonts/Arcadia.ttf") format("truetype"); */
  src: url("./fonts/font.woff2") format("woff2"),
    url("./fonts/font.woff") format("woff");
  font-weight: normal;
}

@font-face {
    font-family: 'GrapeNuts-Regular';
    src: url("./fonts/GrapeNuts-Regular.woff") format("woff"), url('./fonts/GrapeNuts-Regular.ttf') format('truetype');
}

@font-face {
    font-family: 'Montserrat-Italic-VariableFont_wght';
    src: url("./fonts/Montserrat-Italic-VariableFont_wght.woff") format("woff"), url('./fonts/Montserrat-Italic-VariableFont_wght.ttf') format('truetype');
}

@font-face {
    font-family: 'Montserrat-VariableFont_wght';
    src: url("./fonts/Montserrat-VariableFont_wght.woff") format("woff"), url('./fonts/Montserrat-VariableFont_wght.ttf') format('truetype');
}

@font-face {
    font-family: 'ShadowsIntoLight-Regular';
    src: url("./fonts/ShadowsIntoLight-Regular.woff") format("woff"), url('./fonts/ShadowsIntoLight-Regular.ttf') format('truetype');
}

.home-page-desktop {
  background: url("./img/florida-crossover.jpg") no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 100vh;
}

.home-page-mobile {
  background: url("./img/florida-crossover.jpg");
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.info-page {
  background: url("./img/villa-parker-edit.jpeg") no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.rsvp-page {
  background: url("./img/flower_transparent_side.png") no-repeat center center, linear-gradient(white, #B2CAB2);
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.rsvp-page-mobile {
  background-position: right;
}

.rs-btn {
  border-radius: 20px !important;
  color: black !important;
  border: 1px solid black !important;
}

.rs-picker-has-value .rs-btn .rs-picker-toggle-value, .rs-picker-has-value .rs-picker-toggle .rs-picker-toggle-value {
  color: #000000 !important; 
}
.rs-picker-select-menu-item.rs-picker-select-menu-item-active, .rs-picker-select-menu-item.rs-picker-select-menu-item-active:hover {
  color: #000000 !important;
}

.section-header {
    text-align: center;
    font-family: Arcadia;
    padding-top: 50px;
    color: white;
}

.grey-header {
    text-align: center;
    font-family: Arcadia;
    padding-top: 50px;
    color: #4d4d4d;
}

.delete-icon {
  float: right;
  cursor: pointer;
}

.info-header {
    color: black;
}

.info-subheader {
    font-family: Arcadia;
    text-align: center;
    color: white;
    font-weight: bolder;
    font-size: 30px;
}

.info-detail {
    font-family: Montserrat-VariableFont_wght;
    text-align: center;
    color: white;
}

.ourstory-andinfo-detail {
    font-family: Montserrat-VariableFont_wght;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    line-height: 1.6;
}



