@font-face {
  font-family: "Arcadia";
  /* src: local("Arcadia"), url("/src/fonts/Arcadia.ttf") format("truetype"); */
  src: url(/static/media/font.9fb534b9.woff2) format("woff2"),
    url(/static/media/font.f14031b5.woff) format("woff");
  font-weight: normal;
}

@font-face {
    font-family: 'GrapeNuts-Regular';
    src: url(/static/media/GrapeNuts-Regular.c611e27b.woff) format("woff"), url(/static/media/GrapeNuts-Regular.269ac886.ttf) format('truetype');
}

@font-face {
    font-family: 'Montserrat-Italic-VariableFont_wght';
    src: url(/static/media/Montserrat-Italic-VariableFont_wght.ac2cc2c6.woff) format("woff"), url(/static/media/Montserrat-Italic-VariableFont_wght.f578a4cd.ttf) format('truetype');
}

@font-face {
    font-family: 'Montserrat-VariableFont_wght';
    src: url(/static/media/Montserrat-VariableFont_wght.3576cb81.woff) format("woff"), url(/static/media/Montserrat-VariableFont_wght.3068cdb4.ttf) format('truetype');
}

@font-face {
    font-family: 'ShadowsIntoLight-Regular';
    src: url(/static/media/ShadowsIntoLight-Regular.0652d770.woff) format("woff"), url(/static/media/ShadowsIntoLight-Regular.e8c09384.ttf) format('truetype');
}

.home-page-desktop {
  background: url(/static/media/florida-crossover.bdd56c58.jpg) no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
}

.home-page-mobile {
  background: url(/static/media/florida-crossover.bdd56c58.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.info-page {
  background: url(/static/media/villa-parker-edit.5445ce6c.jpeg) no-repeat center center;
  background-size: cover;
}

.rsvp-page {
  background: url(/static/media/flower_transparent_side.e30f8564.png) no-repeat center center, linear-gradient(white, #B2CAB2);
  background-size: cover;
}

.rsvp-page-mobile {
  background-position: right;
}

.rs-btn {
  border-radius: 20px !important;
  color: black !important;
  border: 1px solid black !important;
}

.rs-picker-has-value .rs-btn .rs-picker-toggle-value, .rs-picker-has-value .rs-picker-toggle .rs-picker-toggle-value {
  color: #000000 !important; 
}
.rs-picker-select-menu-item.rs-picker-select-menu-item-active, .rs-picker-select-menu-item.rs-picker-select-menu-item-active:hover {
  color: #000000 !important;
}

.section-header {
    text-align: center;
    font-family: Arcadia;
    padding-top: 50px;
    color: white;
}

.grey-header {
    text-align: center;
    font-family: Arcadia;
    padding-top: 50px;
    color: #4d4d4d;
}

.delete-icon {
  float: right;
  cursor: pointer;
}

.info-header {
    color: black;
}

.info-subheader {
    font-family: Arcadia;
    text-align: center;
    color: white;
    font-weight: bolder;
    font-size: 30px;
}

.info-detail {
    font-family: Montserrat-VariableFont_wght;
    text-align: center;
    color: white;
}

.ourstory-andinfo-detail {
    font-family: Montserrat-VariableFont_wght;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    line-height: 1.6;
}




